import React from "react";
import BannerPost from "./post";
import BannerGet from "./get";
import { useDispatch } from "react-redux";
import { useState } from "react";
import { DeleteBanner, GetBanner } from "../../redux/banner";
import BannerPut from "./put";

function BannerComponent({ open, handleClose }) {
  const dispatch = useDispatch();
  const [adminId, setAdminId] = useState();
  const [openDelete, setOpenDelete] = useState(false);
  const handleCloseDelete = () => setOpenDelete(false);

  const handleDeleteModal = (e) => {
    setOpenDelete(true);
  };

  const HandleDelete = async (e) => {
    await dispatch(DeleteBanner(e.currentTarget.id));
    dispatch(GetBanner());
    handleCloseDelete();
  };

  const [openPut, setOpenPut] = useState(false);

  const onClosePut = () => {
    setOpenPut(false);
  };

  const handlePutModal = async (e) => {
    setOpenPut(true);
    setAdminId(e.currentTarget.id);
  };
  return (
    <>
      <BannerPost Open={open} HandleClose={handleClose} />
      <BannerGet handlePutModal={handlePutModal} HandleDelete={HandleDelete} />
      <BannerPut Open={openPut} HandleClose={onClosePut} putid={adminId} />
    </>
  );
}

export default BannerComponent;
