import { Splide, SplideSlide } from "@splidejs/react-splide";
import React, { useEffect, useRef, useState } from "react";
import { Col, Row } from "react-grid-system";
import { useDispatch, useSelector } from "react-redux";
import styled from "./style.module.css";
import "@splidejs/react-splide/css";
import "@splidejs/react-splide/css/skyblue";
import "@splidejs/react-splide/css/sea-green";
import "@splidejs/react-splide/css/core";
import "./style.css";
import { Image, Popover, Space } from "antd";
function BannerGet({ handlePutModal, HandleDelete }) {
  const dispatch = useDispatch();

  const Data = useSelector((state) => state.banner.BannerGet.data);

  const DataTimer = Data.filter(
    (elem) => elem.banner_category.banner_category_ru === "Продукт дня"
  );

  const Time = DataTimer.map((el) => el?.time);

  window.localStorage.setItem("TimeFor", Time);

  const SECOND = 1000;
  const MINUTE = SECOND * 60;
  const HOUR = MINUTE * 60;
  // const ShiftingCountdown = () => {
  const intervalRef = useRef(null);

  const [remaining, setRemaining] = useState({
    hours: 0,
    minutes: 0,
    seconds: 0,
  });

  useEffect(() => {
    intervalRef.currentTarget = setInterval(handleCountdown, 1000);
    return () => clearInterval(intervalRef.current || undefined);
  }, []);

  const handleCountdown = () => {
    const end = new Date(window.localStorage.getItem("TimeFor"));

    const now = new Date();

    const distance = +end - +now;

    const hours = Math.floor(distance / HOUR);
    const minutes = Math.floor((distance % HOUR) / MINUTE);
    const seconds = Math.floor((distance % MINUTE) / SECOND);

    if (hours.length < 2) hours = "0" + hours;
    if (minutes.length < 2) minutes = "0" + minutes;
    if (seconds.length < 2) seconds = "0" + seconds;

    setRemaining({
      hours,
      minutes,
      seconds,
    });
  };
  return (
    <>
      <div className={styled.wrapper}>
        <Row style={{ margin: 0 }}>
          <Col lg={12}>
            <div className="Splide">
              <Splide
                options={{
                  gap: "20px",
                }}
              >
                {Data.map((elem) =>
                  elem.banner_category.banner_category_ru ==
                  "Баннер карусель" ? (
                    <>
                      <SplideSlide>
                        <div className={styled.Slide}>
                          <div className={styled.left}>
                            <Image
                              width={250}
                              height="100%"
                              style={{
                                aspectRatio: "1 / 1",
                                borderRadius: "20px",
                                objectFit: "cover",
                              }}
                              src={elem.image}
                            />
                            <div className={styled.texts}>
                              <h2>{elem.title_ru}</h2>
                              <p className={styled.Price}>{elem.price} uzs</p>
                              <p>{elem.description_ru}</p>
                            </div>
                          </div>
                          <div className={styled.buttos}>
                            <button
                              id={elem.id}
                              onClick={handlePutModal}
                              className={styled.btnIcon}
                            >
                              <i id={elem.id} class="bx bx-edit-alt"></i>
                            </button>
                            <Space wrap>
                              <Popover
                                trigger="click"
                                placement="rightBottom"
                                content={
                                  <div className="content_delete_box">
                                    <p>
                                      Вы уверены, что хотите удалить эту админ?
                                    </p>
                                    <div className="btn_wrap_delete">
                                      <button
                                        onClick={HandleDelete}
                                        id={elem.id}
                                        className="yes_btn"
                                      >
                                        Удалить
                                      </button>
                                    </div>
                                  </div>
                                }
                                title={
                                  <div className="delete_box">
                                    <i class="bx bxs-error-circle"></i>
                                    <span>
                                      Удалите админ <span>{elem.name}</span>
                                    </span>
                                  </div>
                                }
                              >
                                <div className="btn-wrap">
                                  <button id={elem.id}>
                                    <i class="bx bxs-trash"></i>
                                  </button>
                                </div>
                              </Popover>
                            </Space>
                          </div>
                        </div>
                      </SplideSlide>
                    </>
                  ) : null
                )}
              </Splide>
            </div>
          </Col>
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />

          <Col lg={12}>
            <Row style={{ margin: 0 }}>
              <Col lg={6}>
                <h3 style={{ textAlign: "start", margin: "0px 0 20px 0" }}>
                  Продукт дня
                </h3>
                {Data.map((elem) =>
                  elem.banner_category.banner_category_ru == "Продукт дня" ? (
                    <>
                      <div className={styled.dailyBox}>
                        <div className={styled.info}>
                          <div className={styled.texts}>
                            <h2>{elem.title_ru}</h2>
                            <p>{elem.price} uzs</p>
                          </div>

                          <Image
                            width={150}
                            height="100%"
                            style={{
                              aspectRatio: "1 / 1",
                              borderRadius: "20px",
                              objectFit: "cover",
                            }}
                            src={elem.image}
                          />
                        </div>
                        <div className={styled.settings}>
                          <button id={elem.id} onClick={handlePutModal}>
                            <i id={elem.id} class="bx bx-edit"></i>
                          </button>
                          <button>
                            <p>
                              {remaining.hours < 10 ? <>0</> : null}
                              {remaining.hours}:
                              {remaining.minutes < 10 ? <>0</> : null}
                              {remaining.minutes}:
                              {remaining.seconds < 10 ? <>0</> : null}
                              {remaining.seconds}
                            </p>
                            <i class="bx bx-timer"></i>
                          </button>
                        </div>
                      </div>
                    </>
                  ) : null
                )}
              </Col>
              <Col lg={6}>
                <h3 style={{ textAlign: "start", margin: "0px 0 20px 0" }}>
                  Рекламные баннеры
                </h3>
                {Data.map((elem) =>
                  elem.banner_category.banner_category_ru ==
                  "Рекламные баннеры" ? (
                    <>
                      <div className={styled.dailyBox}>
                        <div className={styled.info}>
                          <div className={styled.texts}>
                            <h2>{elem.title_ru}</h2>
                            <p>{elem.price} uzs</p>
                          </div>

                          <Image
                            width={150}
                            height="100%"
                            style={{
                              aspectRatio: "1 / 1",
                              borderRadius: "20px",
                              objectFit: "cover",
                            }}
                            src={elem.image}
                          />
                        </div>
                        <div className={styled.settings}>
                          <button id={elem.id} onClick={handlePutModal}>
                            <i id={elem.id} class="bx bx-edit"></i>
                          </button>
                        </div>
                      </div>
                    </>
                  ) : null
                )}
              </Col>
            </Row>
          </Col>
        </Row>
      </div>
    </>
  );
}

export default BannerGet;
