import React, { useRef, useState } from "react";
import CommonBtn from "../../common/CommonBtn";
import DrawerCommon from "../../common/Drawer";
import SelectCommon from "../../common/select/index";
import { useDispatch } from "react-redux";
import { Wrapper } from "./styled-index";
import { Row, Col } from "react-grid-system";
import {
  EyeTwoTone,
  EyeInvisibleOutlined,
  LoadingOutlined,
} from "@ant-design/icons";
import { DatePicker, Image, Input, Spin } from "antd";
import InputCommon from "../../common/input";
import { useSelector } from "react-redux";
import "./styles.css";
import TextArea from "antd/es/input/TextArea";
import { PostBanner, UploadBanner } from "../../../redux/banner";

function BannerPost({ Open, HandleClose }) {
  const dispatch = useDispatch();

  const [nameRu, setNameRu] = useState();
  const [nameEn, setNameEn] = useState();
  const [nameUz, setNameUz] = useState();

  const [DesRu, setDesRu] = useState();
  const [DesEn, setDesEn] = useState();
  const [DesUz, setDesUz] = useState();

  const [category, setCategory] = useState();
  const [product, setProduct] = useState();
  const [time, setTime] = useState();
  const [price, setPrice] = useState();
  const [sale, setSale] = useState();

  const HandleChange = (e) => {
    dispatch(UploadBanner(e));
  };

  const uploadDatas = useSelector((state) => state.banner.uploadBanner);

  const HandleSubmit = async (e) => {
    e.preventDefault();

    const body = {
      title_uz: nameUz,
      title_en: nameEn,
      title_ru: nameRu,
      description_uz: DesUz,
      description_en: DesEn,
      description_ru: DesRu,
      price: price,
      time: time,
      sale: sale,
      image: uploadDatas.data,
      banner_category: category,
      products: product,
    };

    await dispatch(PostBanner(body));

    window.location.reload();
  };

  const antIcon = (
    <LoadingOutlined
      style={{
        fontSize: 24,
        color: "#000",
      }}
      spin
    />
  );

  const option = [];

  const DataProducts = useSelector((state) => state.product.productsGet.data);

  DataProducts.map((elem) =>
    option.push({
      label: elem.product_name_ru,
      value: elem.id,
    })
  );

  const options = [
    {
      label: "Баннер карусель",
      value: 1,
    },
    {
      label: "Продукт дня",
      value: 2,
    },
    {
      label: "Рекламные баннеры",
      value: 3,
    },
  ];

  const HandelTime = (e) => {
    setTime(e.target.value);
  };

  return (
    <DrawerCommon title={"Добавить баннер"} open={Open} onClose={HandleClose}>
      <>
        <Wrapper onSubmit={HandleSubmit}>
          <div>
            <div className="input_wrap">
              <Row className="row">
                <Col className="col" lg={4}>
                  <Row className="row">
                    <Col className="col_upload" lg={6}>
                      {uploadDatas.Loading == true ? (
                        <div className="spinss">
                          <Spin indicator={antIcon} />
                        </div>
                      ) : uploadDatas.Success == true ? (
                        <Image
                          style={{
                            aspectRatio: "1 / 1",
                            borderRadius: "20px",
                            zIndex: "99999999",
                            objectFit: "cover",
                          }}
                          src={uploadDatas.data}
                        />
                      ) : (
                        <div className="none_img">
                          <i class="bx bxs-image"></i>
                        </div>
                      )}
                    </Col>
                    <Col className="col_upload" lg={6}>
                      {uploadDatas.Loading == true ? (
                        <div className="spins">
                          <Spin indicator={antIcon} />
                        </div>
                      ) : (
                        <>
                          <input
                            type="file"
                            id="file"
                            onChange={HandleChange}
                          />
                          <label for="file" class="custom-file-upload">
                            <span className="span-download">
                              <ion-icon name="cloud-download-outline"></ion-icon>
                              <span>Загрузить фото</span>
                            </span>
                          </label>
                        </>
                      )}
                    </Col>
                    <Col lg={12}>
                      <div className="infor_box">
                        <p>
                          <span>Формат: </span>PNG, JPEG, JPG, SVG.
                          Рекомендуемое разрешение <span>1080×1080</span>
                        </p>
                        <p>
                          <span>Размер: </span>размер файла не должен превышать
                          5 MB
                        </p>
                      </div>
                    </Col>
                  </Row>
                </Col>
                <Col lg={8}>
                  <Row className="row">
                    <Col className="col" lg={6}>
                      <h4>Категория баннера</h4>
                      <SelectCommon
                        onChange={(e) => setCategory(e)}
                        options={options}
                      />
                    </Col>
                    <Col className="col" lg={6}>
                      <h4>Продукт баннера</h4>
                      <SelectCommon
                        options={option}
                        onChange={(e) => setProduct(e)}
                      />
                    </Col>
                    <Col className="col" lg={6}>
                      <h4>Таймер баннера</h4>
                      <InputCommon
                        onChange={HandelTime}
                        type={"datetime-local"}
                      />
                    </Col>
                    <Col className="col" lg={6}>
                      <h4>Цена на баннер</h4>
                      <InputCommon
                        type={"number"}
                        onChange={(e) => setPrice(e.currentTarget.value)}
                      />
                    </Col>
                    <Col className="col" lg={6}>
                      <h4>Скидка на баннер в процентах</h4>
                      <InputCommon
                        type={"number"}
                        onChange={(e) => setSale(e.currentTarget.value)}
                      />
                    </Col>
                  </Row>
                </Col>
                <Col className="col" lg={12}>
                  <h2>Название баннера</h2>
                  <Row className="row">
                    <Col className="col" lg={4}>
                      <h4>На русском</h4>
                      <InputCommon
                        onChange={(e) => setNameRu(e.currentTarget.value)}
                      />
                    </Col>
                    <Col className="col" lg={4}>
                      <h4>На английском</h4>
                      <InputCommon
                        onChange={(e) => setNameEn(e.currentTarget.value)}
                      />
                    </Col>
                    <Col className="col" lg={4}>
                      <h4>На узбекском</h4>
                      <InputCommon
                        onChange={(e) => setNameUz(e.currentTarget.value)}
                      />
                    </Col>
                  </Row>
                </Col>
                <Col className="col" lg={12}>
                  <h2>Описание баннера</h2>
                  <Row className="row">
                    <Col className="col" lg={4}>
                      <h4>На русском</h4>
                      <TextArea
                        onChange={(e) => setDesRu(e.currentTarget.value)}
                        autoSize={{ minRows: 12, maxRows: 14 }}
                      />
                    </Col>
                    <Col className="col" lg={4}>
                      <h4>На английском</h4>
                      <TextArea
                        onChange={(e) => setDesEn(e.currentTarget.value)}
                        autoSize={{ minRows: 12, maxRows: 14 }}
                      />
                    </Col>
                    <Col className="col" lg={4}>
                      <h4>На узбекском</h4>
                      <TextArea
                        onChange={(e) => setDesUz(e.currentTarget.value)}
                        autoSize={{ minRows: 12, maxRows: 14 }}
                      />
                    </Col>
                  </Row>
                </Col>
              </Row>
            </div>
          </div>
          <CommonBtn
            type={"submit"}
            style={{
              padding: "12px 40px",
              border: "2px solid #fff",
            }}
          >
            Добавить
          </CommonBtn>
        </Wrapper>
      </>
    </DrawerCommon>
  );
}

export default BannerPost;
