import React, { useEffect, useState } from "react";
import HeaderTopCommon from "../../components/common/HeaderTop";
import { WrapperContainer } from "../../style-App";
import { useDispatch } from "react-redux";
import HorizontalSidebar from "../../components/horizontal-sidebar";
import VerticalSidebar from "../../components/vertical-sidebar";
import { GetBanner } from "../../redux/banner";
import BannerComponent from "../../components/banner";
import { ProductsGet } from "../../redux/products";

function Banner() {
  const [open, setOpen] = useState(false);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(GetBanner());
    dispatch(ProductsGet());
  }, []);

  const handleClose = () => setOpen(false);
  const handleOpen = () => setOpen(true);

  return window.localStorage.getItem("checked") == "false" ? (
    <HorizontalSidebar>
      <WrapperContainer style={{ marginTop: "112px" }}>
        <HeaderTopCommon
          title={"Баннер"}
          onClick={handleOpen}
          textBtn={"Добавить баннер"}
        />
        <BannerComponent handleClose={handleClose} open={open} />
      </WrapperContainer>
    </HorizontalSidebar>
  ) : (
    <VerticalSidebar>
      <WrapperContainer>
        <HeaderTopCommon
          title={"Баннер"}
          onClick={handleOpen}
          textBtn={"Добавить баннер"}
        />
        <BannerComponent handleClose={handleClose} open={open} />
      </WrapperContainer>
    </VerticalSidebar>
  );
}
export default Banner;
